	module.exports = {"data":{"site":{"siteMetadata":{"docSections":{"basics":["/basics/introduction/"],"guides":["/guides/quick-start-guide/"],"configurations":["/configurations/overview/","/configurations/details/","/configurations/custom-webpack-config/"],"testing":["/testing/manual-testing/"]}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/404/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/configurations/custom-webpack-config/"},"frontmatter":{"title":"Custom webpack configuration","id":"custom-webpack-config"}}},{"node":{"fields":{"slug":"/configurations/overview/"},"frontmatter":{"title":"Configuration overview","id":"overview"}}},{"node":{"fields":{"slug":"/testing/manual-testing/"},"frontmatter":{"title":"Manual Testing","id":"manual-testing"}}},{"node":{"fields":{"slug":"/configurations/details/"},"frontmatter":{"title":"Detailed configuration overview","id":"details"}}},{"node":{"fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"Introduction","id":"introduction"}}},{"node":{"fields":{"slug":"/guides/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}}]},"markdownRemark":{"html":"<h2 id=\"overview\"><a href=\"#overview\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Overview</h2>\n<p>Wingsuit uses <code>webpack bundles</code> to make webpack configs reusable for differnt apps. </p>\n<p>The goal is to deliver a community driven set of webpack configurations which can easily extend or replaced.</p>\n<p>A webpack bundle is basicly a class which returns a webpack config object. </p>\n<p>To get an overview of all existing webpackBundles\nNew webpack bundles can be referenced inside the <code>wingsuit.config.js</code>. </p>\n<h3 id=\"create-a-new-webpack-bundle\"><a href=\"#create-a-new-webpack-bundle\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Create a new <code>webpack bundle</code></h3>\n<p>Create a new Class which under <code>.wingsuit/webpackBundle</code> extends BaseConfigBundle.</p>\n<pre><code class=\"language-js\">const wingsuitCore = require(\"@wingsuit-designsystem/core\");\n\nclass CustomBundle extends wingsuitCore.BaseWebpackBundle {\n  getSharedWebpackConfig() {\n    // Custom webpack config.\n    return {}\n  }\n}\n\nmodule.exports = CustomBundle;\n</code></pre>\n<p>Register the bundle inside your wingsuit.config.js.</p>\n<pre><code class=\"language-js\">  const CustomBundle = require('./.wingsuit/webpackBundle/CustomBundle');\n  \n  module.exports = {\n    ... \n    webpackBundles: {\n      \"CustomBundle\": CustomBundle\n    },\n    ...\n  };\n</code></pre>\n<h3 id=\"extend-or-replace-existing-config\"><a href=\"#extend-or-replace-existing-config\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Extend or replace existing config?</h3>\n<p>The most common use case is to extend the existing webpackBundles config. Therefor you can use the <code>extend</code> keyword inside the <code>webpack.config.js</code>.</p>\n<h4 id=\"extend\"><a href=\"#extend\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Extend</h4>\n<pre><code class=\"language-js\"></code></pre>","fields":{"slug":"/configurations/custom-webpack-config/"},"frontmatter":{"title":"Custom webpack configuration","id":"custom-webpack-config"}}},"pathContext":{"slug":"/configurations/custom-webpack-config/"}}

